body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(1deg, purple 0%, gold 100%);
  background: linear-gradient(89deg, purple 0%, gold 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Tilt{
    background: -webkit-linear-gradient(1deg, gold 30%, purple 60%);
    background: linear-gradient(89deg, gold 30%, purple 60%);
}
.form{
    max-width: 700px;
    width: 90%;
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAN0lEQVQYlWNggAIf/ov/GdAANjG4BDImShFWxciCuNh4TUNRiKwbl2d8+C/+J14hruDA6WuqAQCD504NQqyB1QAAAABJRU5ErkJggg==) repeat;
}
.bounding-box{
    position: absolute;
    box-shadow: inset 0 0 0 3px #149df2;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}
.App {
  text-align: center;
}

.center{
  display: flex;
  justify-content: center;
}

.particles{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}

